html {
  font-family: 'Mulish', sans-serif;
  margin:0;
  padding:0;
}


body {
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-size: 18px;
  letter-spacing: 0.05rem;
  color: #181717;
  font-weight:600;
}


.light-theme {
  background-color: #fff;
  margin:0;
  padding:0;
}

.dark-theme {
  background-color: #2a292c;
  color: #f8f8f8;
  margin:0;
  padding:0;
}

.dark-theme nav {
  background-color: #2a292c;
}

.light-theme nav a {
  text-decoration: none; 
  color: #181717;
  font-weight: bold;
}

.dark-theme a:link, a:visited, a:active  {
  text-decoration: none; 
  color: #eeedee;
  font-weight: bold;
}

.light-theme a:hover  {
  color: #FC5050;
}

.dark-theme a:hover  {
  color: #FC5050;
}

.light-theme .header {
  height: 40vh;
  background-color: #eeeeee ;
  padding: 2rem;
  margin-top: 1rem;
}

.dark-theme .header {
  height: 40vh;
  background-color:#34353a;
  padding: 2rem;
  margin-top: 1rem;
}

h1 {
  font-size:40px;
}

.dark-theme h1 {
  margin:0;
  font-size: 40px;
  color:#f8f8f8;
}

button {
  border:none;
  background: transparent;
  cursor:pointer;
}

footer {
  position:sticky;
  display:flex;
  justify-content: center;
  margin-top:5rem;
}

footer a {
  padding: 1rem;
}

/*NAVIGATION*/

nav {
  display: grid;
  row-gap: 1rem;
  padding-top: 1rem;
}

nav>:nth-child(1) {
  justify-self: right;
  margin-right: 20px;
}

nav>:nth-child(2) {
  justify-self: center;
}

.menuItems {
  display: inline-flex;
  justify-content: space-evenly;
}

@media (min-width: 40rem) {
  nav {
    grid-template-columns: 1fr auto auto;
    row-gap: 0;
  }

  nav>:nth-child(1) {
    grid-column-start: 3;
  }

  nav>:nth-child(2) {
    justify-self: left;
    margin-left: 1rem;
  }

  nav>:nth-child(3) {
    align-self: center;
    font-size: 25px;

  }

  nav a {
    padding: 1rem;
  }

  body {
    font-size: 18px;
  }
}

@media (max-width: 335px) {
  .menuItems {
    align-items: center;
    flex-flow:column;
    row-gap:0.5rem;
  } 
 }

  /*PAGES*/

.content {
box-sizing: border-box;
margin-top:2rem;
padding: 0 15vw;
background-color: #fff;
height: fit-content;
}

.dark-theme .content {
  background-color: #2a292c;
  color: #f8f8f8;
  }

hr {
  margin:auto;
  width:70vw;
  margin-bottom:1rem;
}

  /*HOME*/
  .posts {
    box-sizing: border-box;
    display:flex;
    flex-flow: column;
    align-items:center;
  }

  .post {
    box-sizing: border-box;
    padding: 1rem;
    display: flex;
    font-weight:700; 
    column-gap: 1rem;
  }

  time {
    font-weight:200;
  }

  @media (min-width: 700px) {
    .post {
      box-sizing: border-box;
      column-gap: 2rem;
    } 
   }